                      ></h2>
                    </div>
                  </b-col>
                </b-row>
                <b-row>
                  <b-col
                    cols="12"
                    lg="12"
                    :order="order"
                    class="align-self-end"
                  >
                    <SearchField />
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-container>
    </div>
  </main>
</template>

<script>
export default {
  name: 'HomeBanner',
  components: {
    SearchField: () => import('@/components/home/SearchField.vue'),
  },
  data() {
    return {
      order: 0,
    }
  },
  mounted() {
    window.addEventListener('resize', () => {
      if (window.innerWidth <= 767) {
        this.order = 1
      } else {
        this.order = 0
      }
    })
  },
}
</script>

